main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Lucida Console', 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif, Helvetica, Arial, sans-serif; 

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--dark);
  color: var(--light);
  font-family: 'Courier'

}

iframe {
  height: 1000px;
  width: 90%;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
}

html,
body {
  min-height: 100%;
}


h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Times New Roman', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--light);
  padding: 20px 35px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

header a {
  color: var(--dark);
}



button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

.image {
  margin: auto;
  max-width: 100%;
  width: 430px;
  height: 350px;
    object-fit: cover;

}



.aboutImage {
  width: auto;
  max-width: 600px;
  height:auto;
  max-height: 500px;
}

.image:hover {
  opacity: 0.6;
  transition: opacity .1s ;

}

.work-box {
  display: block;
  padding-top: 50px;
  padding-bottom: 60px;
  height:550px;
  width: 600px;
  margin: auto;

}



.img-txt {
}

.img-txt h3 {
  font-size: 20px;
}

.img-txt p{
  font-size: 14px;

}

span[role="img"] {
  cursor: default;
}

form {
  width: 20%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
}

h2 {
  font-size: 40px;
  font-weight: bolder;
}

nav {
  margin-right: 20px;

}

nav a:hover {
  text-decoration: none;
}

nav ul li {
  cursor: pointer;
  list-style: none;
  color: black;
}

nav ul span {
  padding: 10px;
}

nav ul span:hover {
  background: yellow;
  border-radius: 20px;
  color: black;
}

.first-project {
  text-align: center;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.nav-row {
  display: flex;
  flex-wrap: wrap;
}

.about {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 50px;
    width: 60%;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 50px;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: var(--secondary);
}

footer {
  color: white;
  background: black;
  width: 100%;
  padding: 40px 35px;
  display: flex;
  justify-content: center;
}

.medialogo {
  text-align: center;
  width: 60px;
  height: auto;
  padding-bottom: 10px;
  padding-left: 20px;
}

.section-info {
  text-align: center;
}

.foot {
  margin-left: 50px;
  align-items: center;
}


.section-title {
  text-align: center;
  border-bottom: 3px solid white;
  margin-right: auto;
  margin-left: auto;
  width:50%;
}

.project-work {
  display: flex;
  flex-wrap: wrap;
}

.resumeLink {
  padding: 20px;
  margin-top: 20px;
  font-size: 30px;
  /* color: var(--secondary); */
  text-decoration: none;
}

.skillLook {
  width: 80%;
  margin-left: auto;
}

.skillLook h3 {
  font-size: 25px;
  font-weight: bold;
}

.skillLang  {
  display: flex;
  flex-wrap: wrap ;
  margin-top: 5px;
  margin-left: 60px;
}

.skillLang li {
  margin-right: 10px;
  color: white;
  border-radius: 5px;
  padding: 5px;
  padding-right: 50px;
}

.form {



}

.formSection {
  margin-left: auto;
  margin-right: auto;
  width: 50%
}

.flex-row {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 50px;
  width: 60%;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-top: 50px;
}

/* MEDIA QUERY FOR SMALLER DESKTOP SCREENS AND SMALLER */
@media screen and (max-width:1280px) {

  .image {
    margin: auto;
    max-width: 100%;
    width: 400px;
    height: 350px;
    object-fit: cover;
  
  }
  
  .work-box {
    display: block;
    padding-top: 50px;
    padding-bottom: 60px;
    height:550px;
    width: 500px;
    margin: auto;
  
  }

}


/* MEDIA QUERY FOR SMALLER DESKTOP SCREENS AND SMALLER */
@media screen and (max-width:900px) {

  .aboutImage {
    width: auto;
    max-width: 500px;
    height:auto;
    max-height: 400px;
  }

  .formSection {
    margin-left: auto;
    margin-right: auto;
    width: 80%
  }

  header {
    padding:0;
    justify-content:center;
    position: relative;
  }

  header h1 {
      width: 100%;
      text-align: center;
      font-size: 30px;
  }

  header nav ul {
      margin-top: 20px;
      width: 100%;
      justify-content: center;
  }

  iframe {
    height: 600px;
    width: 90%;
  
  }


}



/* MEDIA QUERY FOR MOBILE PHONES AND SMALLER */
@media screen and (max-width: 550px) {

  .aboutImage {
    width: auto;
    max-width: 400px;
    height:auto;
    max-height: 300px;
  }

  .flex-row {

    width: 80%;

  }

  iframe {
    height: 450px;
    width: 90%;
  
  }

  .section-info {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;  }

    textarea {
      width: 300px;
      height: 120px;
      border: 3px solid #cccccc;
      padding: 5px;
    }
}